/********************************************* 폰트 모음 ************************************************/


/********************************************* 헤더 부분 ************************************************/

header { 
  height: 300px;
  border-bottom: 1px solid rgba(0,0,0,0.3);
}

/* 헤더 로고 */
header .logo {
  max-width: 1400px;
  width: 100%;
  height: 180px;
  margin: auto;
  display: flex;
  align-items: end;
}

header .logo p {
  padding: 2rem;
  font-size: 40px;
  font-weight: bold;
  color: #ef6d1f;
}


/*  헤더 메뉴  */
header .menu {
  max-width: 1400px;
  width: 100%;
  margin: auto;
  display: flex;
}

header .menu ul {
  display: flex;
  width: 70%;
}

header .menu .search {
  width: 30%;
}

header .menu ul li {
  padding: 1rem 2rem;
  cursor: pointer;
}

header .menu ul li p {
  font-size: 20px;
}


/* 헤더 검색창 */
header .search {
  display: flex;
  align-items: center;
  justify-content: end;
}

header .search input {
  width: 200px;
  height: 35px;
}

header .search .search_btn {
  width: 50px;
  height: 35px;
  background-color: #ef6d1f;
  border: transparent;
  margin-left: -1px;
}


/********************************************* 메인 배너 ************************************************/
.main-banner {
  border-bottom: 1px solid rgba(0,0,0,0.3);
  width: 100%;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-banner p {
  font-size: 100px;
  font-weight: bold;
}


/******************************************** 메인 이벤트 ***********************************************/
.main-event {
  max-width: 1400px;
  width: 100%;
  margin: auto;
  margin-top: 180px;
  padding: 0 2rem;
}

.main-event .main-event-logo {
  display: flex;
  align-items: center;
}

.main-event .main-event-logo h1 {
  font-size: 30px;
  color: #ef6d1f;
  padding: 2rem 1rem;
}

.main-event .main-event-logo img {

}

.main-event .event {
  display: flex;
  justify-content: space-between
}

.main-event .event .event-img {
  border: 1px solid blue;
  width: calc(100% / 3 - 10px);
  height: 240px;
}


/********************************************* 게시판 ************************************************/
.community {
  max-width: 1400px;
  margin: auto;
  padding: 0 2rem;
  margin-top: 180px;
}

.community .community-logo {
  display: flex;
  align-items: center;
}

.community .community-logo h1 {
  font-size: 24px;
  color: #ef6d1f;
  padding: 2rem 1rem;
}

.community .community-logo strong {
  font-size: 30px;
}

.community .community-board {
  display: flex;
  justify-content: space-between;
}

.community .community-content {
  border: 1px solid black;
  width: calc(100% / 3 - 10px);
  height: 200px;
  display: flex;
  padding: 1rem;
}

.community .community-content .community-content-img {
  border: 1px solid red;
  width: 160px;
}

.community .community-content .community-content-p {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 1rem;
}

.community .community-content .community-content-p > div h1 {
  font-weight: bold;
  font-size: 18px;
}

.community .community-content .community-content-p > div h2 {
  margin-top: 30px;
  line-height: 20px;
  color: rgba(0,0,0,0.6);
}

.community .community-content .community-content-p p {
  line-height: 18px;
  font-weight: bold;
}

.community .community-content .community-content-p p strong {
  font-weight: normal;
  color: rgba(0,0,0,0.6);
}

.community .community-btn {
  display: flex;
  justify-content: end;
  width: 100%;
  margin-top: 15px;
}

.community .community-btn button {
  width: 60px;
  height: 30px;
  background-color: transparent;
  border: 1px solid rgba(0,0,0,0.3);
  cursor: pointer;
}


/********************************************* 멍사진 ************************************************/

.gallery {
  max-width: 1400px;
  width: 100%;
  margin: auto;
  margin-top: 160px;
  padding: 0 2rem;
}

.gallery .gallery-logo {
  display: flex;
  align-items: center;
}

.gallery .gallery-logo h1 {
  font-size: 24px;
  color: #ef6d1f;
  padding: 2rem 1rem;
}

.gallery .gallery-logo h1 strong {
  font-size: 30px;
}

.gallery .dog-gallery {
  display: flex;
  justify-content: space-between;
}

.gallery .dog-gallery .gallery-content {
  border: 1px solid red;
  width: calc(100% / 5 - 10px);
  height: 200px;
}

.gallery .gallery-btn {
  display: flex;
  justify-content: end;
  width: 100%;
  margin-top: 15px;
}

.gallery .gallery-btn button {
  width: 60px;
  height: 30px;
  background-color: transparent;
  border: 1px solid rgba(0,0,0,0.3);
  cursor: pointer;
}



/***************************************  공지사항 & 멍튜브  *******************************************/

.two-content {
  max-width: 1400px;
  width: 100%;
  margin: auto;
  margin-top: 180px;
  height: 800px;
  display: flex;
  justify-content: space-between;
  padding: 2rem;
}

.two-content > div {
  width: calc(100% / 2 - 60px);
}


/* 공지사항 */
.two-content .notice > h1 {
  font-size: 24px;
  color: #ef6d1f;
  padding: 2rem 0 3rem 0;
}


.two-content .notice .notice-board {
  width: 100%;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}


.two-content .notice .notice-board .notice-content {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  padding: 0 2rem;
  background-color: #f5f4f2;
  border-top: 1px solid white;
}

.two-content .notice .notice-board > div:nth-of-type(1) {
  border-top: transparent;
}


/* 멍튜브 */
.two-content .video > h1 {
  font-size: 24px;
  color: #ef6d1f;
  padding: 2rem 0 3rem 0;
}

.two-content .video .video-board {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.two-content .video .video-board > div {
  width: calc(100% / 2 - 30px);
  background-color: #f5f4f2;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}



/********************************************* 제휴문의 ************************************************/

.contact {
  position: relative;
  width: 100%;
  height: 600px;
  background-color: #ed6d1f;
  display: flex;
  align-items: end;
}

.contact .contact-main {
  position: relative;
  border: 1px solid black;
  width: 1000px;
  height: 500px;
  background-color: white;
}


.contact .contact-main h1 {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 50px;
  font-weight: bold;
}


.contact .contact-sub {
  position: relative;
  width: 400px;
  height: 100%;
}



.contact .contact-sub button {
  border: 1px solid black;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 40px;
  width: 300px;
  height: 80px;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.contact .contact-sub button p {
  font-size: 24px;
  display: flex;
  align-items: center;
}

.contact .contact-sub button i {
  font-size: 20px;
  font-weight: 100;
  margin-left: 20px;
  color: rgba(0,0,0,0.5);
}






/*############################################# 이벤트페이지 ###############################################*/

.eventpage {
  max-width: 1400px;
  width: 100%;
  margin: auto;
  padding: 2rem;
  margin-top: 100px;
}

.eventpage .event-main-character {
  position: relative;
  width: 100%;
}

.eventpage .event-main-character p {
  position: absolute;
  bottom: 15%;
  left: 50%;
  font-size: 2vw;
  color: #ef6d1f;
  transform: translate(-50%, -15%);
}

.eventpage .event-main-character img {
  width: 100%;
}







@media (max-width: 1420px) {

  .community .community-content .community-content-img {
    width: 140px;
  }

  .two-content {
    flex-direction: column;
    height: 900px;
  }

  .two-content > div {
    width: 100%;
  }


  .contact {
    margin-top: 180px;
    flex-direction: column;
    height: 700px;
  }

  .contact .contact-main {
    width: 100%;
  }

  .contact .contact-sub {
    position: relative;
    width: 100%;
    height: auto;
    padding: 4rem 0;
  }

  .contact .contact-sub button {
    position: relative;
  }



}




@media (max-width:1020px) {

  header {
    width: 100%;
  }

  header .menu {
    flex-direction: column;
  }

  header .menu ul {
    width: 100%;
  }

  header .menu ul li p {
    font-size: 16px;
  }

  header .menu .search {
    width: 100%;
    justify-content: end;
    padding-right: 2rem;
  }

  header .search input {
    width: 140px;
  }
  .main-event .event > div:nth-of-type(3) {
    display: none;
  }
  .main-event .event .event-img {
    width: calc(100% / 2 - 10px);
  }

  .community .community-board > div:nth-of-type(3) {
    display: none;
  }
  .community .community-content {
    width: calc(100% / 2 - 10px);
  }

  .gallery .dog-gallery .gallery-content {
    width: calc(100% / 3 - 10px);
  }

  .gallery .dog-gallery > div:nth-of-type(4),
  .gallery .dog-gallery > div:nth-of-type(5) {
    display: none;
  }

  .contact .contact-main h1 {
    top: -25px;
    font-size: 45px;
  }

  .two-content .video .video-board > div {
    width: calc(100% / 2 - 10px);
  }

  .contact .contact-sub button {
    width: 240px;
    height: 70px;
  }

  .contact .contact-sub button p {
    font-size: 16px;
  }

  .contact .contact-sub button i {
    font-size: 16px;
  }


}


@media (max-width:640px) {

  header { 
    display: flex;
    height: 100px;
  }









}




/* 1920px이상 큰화면 */
@media (min-width:1921px) {
  
  .contact {
    justify-content: center;
    
  }



}



